.stepOne .submit {
    margin-top: 0px;
}

.stepOne .submit h5 {
    text-align: center;
}

.stepOne .note {
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 8px;
    color: #A5A5A5;
    cursor: pointer;
}

.stepOne .nextSection {
    display: flex;
    justify-content: center;
    align-items: center;
}

.nextBtn {
    display: flex;
    justify-content: center;
    align-items: center;
}

.nextBtn button {
    border: none;
    text-align: center;
    cursor: pointer;
    width: 35px;
    margin: auto;
    background-color: transparent;
}

.nextBtn .nextImg {
    width: 30px;
}